<template>
  <!-- hash -->
  <h2 v-if="item.type === 'hash'" class="item-hash" :id="item.text | generateId" :data-text="item.text">
    <router-link class="header-anchor" :to="{ path: $route.path, hash: `#${item.text}` }">
      {{ item.text }}
    </router-link>
  </h2>
  <div v-else class="content-item">
    <!-- title -->
    <p v-if="item.type === 'title'" class="item-title">{{ item.text }} </p>
    
    <div v-if="item.text === '接口参数'" style="margin-top: 15px;">
      <i style="font-size:larger;color: #409EFF;" class="el-icon-s-opportunity"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">1. 创建API KEY</el-link> <i
        class="el-icon-right"></i>
        <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">2. 配置API KEY权限</el-link> <i
        class="el-icon-right"></i>
      <el-link href="/#/swapV2/authentication.html" target="_blank">3. 了解签名认证</el-link> <i class="el-icon-right"></i>
      <i class="el-icon-right">4. 跑以下示例代码</i>
      <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#常见错误码" target="_blank">5. 了解常见错误码</el-link> <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#频率限制" target="_blank">6. 了解频率限制</el-link> <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#时间戳规范" target="_blank">7. 了解请求时间戳</el-link>
    </div>
    <div v-if="item.text === '接口參數'" style="margin-top: 15px;">
      <i style="font-size:larger;color: #409EFF;" class="el-icon-s-opportunity"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">1. 創建API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">2. 配置API KEY權限</el-link> <i
        class="el-icon-right"></i>
      <el-link href="/#/swapV2/authentication.html" target="_blank">3. 了解簽名認證</el-link> <i class="el-icon-right"></i>
      <i class="el-icon-right">4. 跑以下示例代碼</i>
      <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#常见错误码" target="_blank">5. 常見錯誤碼</el-link> <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#频率限制" target="_blank">6. 了解頻率限制</el-link> <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#时间戳规范" target="_blank">7. 了解請求時間戳</el-link>
    </div>
    <div v-if="item.text === 'request parameters'" style="margin-top: 15px;">
      <i style="font-size:larger;color: #409EFF;" class="el-icon-s-opportunity"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">1. Create API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">2. Configure API KEY permissions</el-link> <i
        class="el-icon-right"></i>
      <el-link href="/#/swapV2/authentication.html" target="_blank">3. Understanding signature authentication</el-link>
      <i class="el-icon-right">4. Run the following example code</i>
      <i class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#常见错误码" target="_blank">5. Understand common error codes</el-link> <i
        class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#频率限制" target="_blank">6. Understand rate limitations</el-link> <i
        class="el-icon-right"></i>
      <el-link href="/#/swapV2/base-info.html#时间戳规范" target="_blank">7. Understanding request timestamps</el-link>
    </div>
    <!-- </div> -->

    <!-- 普通文本 -->
    <div v-if="item.type === 'normal'" class="item-normal">
      <p :style="item.style || {}" v-if="item.text">{{ item.text }}<label v-if="item.text === 'HTTP 请求' || item.text === 'HTTP 請求' || item.text === 'HTTP Request'" > https://open-api.bingx.com</label></p>
      <ul v-if="Array.isArray(item.children)" style="margin-left: -10px;">
        <li v-for="(c1, i) in item.children" :key="i">
          <span v-if="c1.type === 'html'" v-html="c1.text" />
          <span v-else>{{ c1.text }}</span>
          <ul v-if="Array.isArray(c1.children)" style="margin-left: -15px; list-style-type: disc">
            <li v-for="(c2, j) in c1.children" :key="j">{{ c2.text }}</li>
          </ul>
        </li>
      </ul>
      
    </div>

    <!-- html 类型 -->
    <p v-if="item.type === 'html'" v-html="item.text"></p>

    <!-- warning 类型 -->
    <p v-if="item.type === 'warning'" class="item-warning-content">{{ item.text }}</p>

    <!-- code 类型 -->
    <p v-if="item.type === 'code'" class="item-code-content" style="position: relative">
      <i class="el-icon-copy-document" v-clipboard:copy="item.text" v-clipboard:success="onCopy"
        v-clipboard:error="onError" />
      {{ item.text }}
    </p>

    <!-- demo 类型：用于展示具体的接口信息和代码 -->
    <Demo v-if="item.type === 'demo' && $router.currentRoute.path.indexOf('/socket/') < 0" :lang="lang" :item="item" />
    <DemoSocket v-if="item.type === 'demo' && $router.currentRoute.path.indexOf('/socket/') > -1" :lang="lang"
      :item="item" />
    <!-- <Demo v-if="item.type === 'demo'" :lang="lang" :item="item" /> -->
  </div>
</template>

<script>
import { msgMap, generateId } from '@/utils'

import Demo from './Demo.vue'
import DemoSocket from './DemoSocket.vue'

export default {
  name: "ContentItem",
  props: {
    lang: String,
    item: {
      type: Object,
      default: () => { }
    }
  },

  components: {
    Demo,
    DemoSocket,
  },
  data() {
    return {

    }
  },

  filters: {
    generateId
  },

  computed: {
    successMsg() {
      return msgMap['success'][this.lang]
    },

    errorMsg() {
      return msgMap['error'][this.lang]
    },
  },

  methods: {
    onCopy() {
      this.$message.success(this.successMsg)
    },
    onError() {
      this.$message.success(this.errorMsg)
    },
  },
}
</script>

<style>
.content-item {}

.item-hash {
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
}

.item-hash a,
.item-hash a:hover {
  text-decoration: none;
  color: #000000;
}

.item-title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: black;
  margin: 20px 0px;
}

.item-warning-content {
  display: inline-block;
  /* margin: 16px 0px; */
  margin: 16px 0 0 0;
  background: #FFD677;
  border-radius: 8px;
  padding: 16px;
}

.span-code {
  padding: 2px 4px;
  color: #1f1e24;
  background-color: #ececef;
  border-radius: 4px;
}

.item-code-content {
  background: #F9F9F9;
  border-radius: 8px;
  padding: 24px;
}

.item-code-content i {
  position: absolute;
  top: 5px;
  right: 10px;
  cursor: pointer;
}</style>


